@use 'colours';

//...........................................


.container {
    display: flex;
    margin: 0 auto;
    max-width: 80%;
    gap: 40px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 25px;

    a {
        margin-bottom: 20px;
        border: none;
    }
}

//--------------------------------------------- Grade 1-3

.grade-english {
    display: flex;
    justify-content: center;
       
    h1 {
        text-align: center;
    }



}

.container-english {
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    gap: 10px;
    justify-content: center;
    flex-direction: column;
    
    @media screen and (min-width: 745px)  {
        flex-direction: row;
     }

}

.column-left {
    text-align: center;

    @media screen and (min-width: 745px)  {
        width: 460px;
        text-align: left;
     }
   
}

.column-right {
    width: 300px;
    display: flex;
    justify-content: center;
}

.english {
    margin-bottom: 40px;

    li {
        margin-top: 10px;
        font-weight: 700;
        font-size: 16px;
        text-align: left; 
    }
  }



.photo-plan {
    max-width: 90%;
}

.sign-up-plan {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.container-sport {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 60%;
    margin: 0 auto;
    text-align: center;

}

.photo-sport {
    display: flex;
    justify-content: center;
    margin-bottom: 60px;
}

.group {
    font-weight: 700;
}

.grade-skate {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;

    h2 {
        margin-top: 0;
    }
}

.photo-skate {
    max-width: 60%;
}

.list-stuff {
    text-align: left;
}

.camp {
    margin-bottom: 80px;
}


